@mixin itemBorder{
  border: 1px solid #ddd;
  border-collapse: collapse;
  width: 100%;
}
@mixin flex {
  display: flex;
  justify-content: center;
  align-content: center;
justify-items: center;
align-items: center;
}
$headerFooterColor: #61dafb;
#wrapper{
  @include flex();
transition: opacity 0.2s ease;

}
body{
  height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.thumbnailCart{
  width: 20px;
}
.payLogo{
  width: 200px;
}
#paypalBtn{
background-color: #ffd500;
}
#klarnaBtn{
background-color: #ffa7d0;
}
input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.logo{
font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
font-size: 1.5em;
color: #000000;
text-decoration: none;

}
.thumbnail{

  margin: 50px;
  width: 150px;
}


.cartIcon{
  margin-right: 8px;
  width: 30px;
}
h3{
  margin: 0;
}
.single{
  text-align: center;
}
img{
  width: 300px;
}
#products{
display: grid;
grid-template-columns: auto auto auto;
}
.productDescription{
  max-width: 500px;
}
.thumbnailImage{
  width: 150px;
  height: 250px;

}
button{
  background: red;
  color: white;
  border-radius: 20px;
font-family: Arial, Helvetica, sans-serif;
font-size: 1.5em;
border: none;
padding: 8px;
margin: 5px;
}
.cartlink{
  float: right;
}

#cartItems {
 @include itemBorder()
}
#checkoutItems {
  @include itemBorder()
}

#cart{
  display: inline-block;
}
td, th {
  border-bottom: 1px solid #ddd;
  padding: 5px;
}
th { 
  padding-bottom: 10px;
  padding-top: 10px;
  text-align: left;
  background-color: #f74a4a;
  color: white;
}
#headerCart{
  position: absolute;
  top: 0;
  right: 0;
}
header{
 background-color: $headerFooterColor;
  height: 70px;
}
footer{
 background-color: $headerFooterColor;

}
@media only screen and (max-width: 500px) {
  #wrapper{
display: block;
  }
  
  #products{
    grid-template-columns: auto;
    }
    .thumbnail{
     margin: 0;
     padding-bottom: 20px;
     padding-top: 20px;
      border-bottom: 2px solid gray;
      width: 100vw;
      text-align: center;
    }
    .thumbnailImage{
      width: 300px;
      height: unset;
    }
   
}
@media only screen and (max-width: 750px)and (min-width: 501px) {
  #products{
    display: grid;
    grid-template-columns: auto auto;
    }
}
.cartImg{
  width: 100px;

}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  flex-direction: column;
@include flex();
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
